.Exercise {
  padding: 120px 0;
  text-align: center;

  img {
    display: block;
    width: 100%;
  }

  .pageLogo {
    margin: auto;
    width: 160px;
  }

  .pageText {
    text-align: center;
    font-size: 44px;
    font-weight: bold;
    margin-top: 40px;

    @media screen and (max-width: 375px) {
      font-size: 30px;
    }
  }

  .subText {
    color: #5e3dea;
    margin-top: 20px;

    @media screen and (max-width: 375px) {
      font-size: 14px;
    }
  }

  .userDietInfo {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: 1px solid #ededed;
    border-radius: 16px;
    margin-top: 40px;
    padding: 34px 30px 30px 30px;

    .userNotice {
      display: flex;
      align-items: center;
      width: 100%;
      justify-content: center;
      flex-wrap: wrap;
      .badgeInner {
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .badge {
        margin-right: 12px;
        img {
          width: 30px;
          height: 30px;
        }
      }

      .userCal {
        font-size: 40px;
        font-weight: bold;
        margin-bottom: 10px;
      }
    }
    .userNutrient {
      display: flex;
      gap: 5px;

      .NutrientCircle {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100px;
        height: 100px;
        border-radius: 16px;
        background-color: #5e3dea;
        color: #fff;

        .nutrientText {
          font-size: 16px;
          white-space: nowrap;
        }

        .nutrientNum {
          font-size: 34px;
          font-weight: bold;

          .fontSizeThin {
            font-size: 22px;
            font-weight: bold;
          }
        }
      }
    }
  }
  .dietContentWrapper {
    .dietContainer,
    .dietContaine {
      position: relative;
      padding: 80px 30px 30px 30px;
      width: 100%;
      border: 1px solid #ededed;
      border-radius: 16px;
      margin-top: 60px;

      .todayText {
        font-size: 25px;
        font-weight: 800;
        margin-bottom: 20px;
      }

      .todayDate {
        color: #5e3dea;
        font-size: 20px;
        margin-bottom: 12px;
      }
      .timeText {
        font-size: 24px;
        margin-bottom: 24px;
      }
    }
  }
}
