.order {
  padding: 120px 0;

  .subscribeInfo {
    .subscribeTitle {
      margin-bottom: 20px;
      font-size: 24px;
      font-weight: bold;
    }

    .subscribeDiv {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 30px 30px 70px 30px;
      border-bottom: 1px solid #ccc;
      width: 100%;
      flex-wrap: wrap;
      gap: 70px;

      .subscribeImgAndMonth {
        .subImg {
          display: block;
          width: 100px;
        }
      }

      .subTable {
        width: 100%;
        text-align: center;

        tr td {
          padding: 20px;
          border: 1px solid black;
        }

        @media screen and (max-width: 525px) {
          display: none;
        }
      }

      .subTableA {
        display: none; // 기본적으로 숨김
        width: 100%;
        text-align: center;

        tr td {
          padding: 20px;
          border: 1px solid black;
        }

        @media screen and (max-width: 525px) {
          display: block; // 480px 이하에서만 보임

          thead,
          tbody,
          tr,
          th,
          td {
            display: block;
          }

          tr {
            margin-bottom: 10px;
          }

          td {
            text-align: right;
            position: relative;
            padding-left: 50%;
            border: none;
            border-bottom: 1px solid black;
          }

          td:before {
            content: attr(data-label);
            position: absolute;
            top: 20px;
            left: 6px;
            width: 45%;
            padding-right: 10px;
            white-space: nowrap;
            text-align: left;
            font-weight: bold;
          }
        }
      }
    }
  }

  .paymentMethod {
    margin-top: 70px;

    .methodTitle {
      margin-bottom: 20px;
      font-size: 24px;
      font-weight: bold;
    }

    .selectMethod {
      display: flex;
      justify-content: center;
      padding: 30px 30px;
      flex-wrap: wrap;
      width: 100%;

      .buttonDiv {
        padding: 4px;
        width: 250px;
        button {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          height: 56px;
          padding: 11px 22px;
          border: 1px solid #ccc;
          border-radius: 4px;
          font-size: 15px;
          font-weight: 400;
          background-color: white;

          img {
            max-width: 160px;
          }
        }
      }
    }
  }
}
