.nav {
  display: flex;
  position: fixed;
  justify-content: center;
  top: 0;
  left: 0;
  z-index: 3;
  width: 100%;
  background-color: unset;
  transition: all 0.4s;

  .navinner {
    display: flex;
    justify-content: space-between;
    max-width: 1200px;
    width: 100%;
    align-items: center;
    padding: 10px;

    @media screen and (max-width: 470px) {
      display: flex;
      width: 100%;
      justify-content: center;
    }

    .logoSection {
      width: 100px;

      @media screen and (max-width: 470px) {
        display: none;
      }
      img {
        display: block;
        width: 100%;
        cursor: pointer;
      }
    }
    .navList {
      height: 70px;
      padding: 30px 0px;

      .userGrade {
        width: 20px;
        margin-left: 25px;
      }

      button {
        border: 0;
        font-size: 16px;

        background-color: unset;
        color: black;

        @media screen and (max-width: 550px) {
          font-size: 12px;
        }

        &:hover {
          opacity: 0.5;
          cursor: pointer;
        }
      }
      button.black {
        color: #333;
      }

      img {
        display: block;
        width: 100%;
        cursor: pointer;
      }

      .btnLogAuto {
        border: 0;
        font-family: 'Pretendard';
        font-size: 14px;
        color: #333;
        cursor: pointer;
        margin-left: 0;

        .userNickname {
          color: #5e3dea;
          font-weight: bold;
        }
      }

      .btnSignUp {
        border: 0;
        margin: auto;
        text-decoration-line: none;
        font-family: 'Pretendard';
        font-size: 14px;
        color: #333;
        cursor: pointer;
      }
    }
  }
}
.nav.white {
  background-color: #fff;
  transition: all 0.4s;
}
