@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');
* {
  box-sizing: border-box;
}

#root {
  overflow-x: hidden;
}
body {
  position: relative;
  font-family: Pretendard, 'Noto Sans KR', sans-serif;
  font-size: 18px;
  color: #333;
  accent-color: #5e3dea;
}
button,
input,
label,
input::placeholder {
  font-family: 'Pretendard';
}
button {
  cursor: pointer;
}
.sectionInner {
  max-width: 1200px;
  width: 100%;
  margin: auto;
}
.flexCenter {
  display: flex;
  justify-content: center;

  @media screen and (max-width: 870px) {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
}
