.Main {
  padding-bottom: 80px;
  img {
    display: block;
    width: 100%;
    object-fit: cover;
  }

  .sectionInner {
    max-width: 1200px;
    width: 100%;
    margin: auto;
  }

  .mainBanner {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: #000;
    overflow: hidden;

    @media screen and (max-width: 380px) {
      background-color: #a1a6e9;
    }

    img {
      width: 100%;
      height: 100%;
      aspect-ratio: 1/1;

      @media screen and (max-width: 348px) {
        display: none;
      }
    }

    .btnMain {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      display: block;
      background-color: unset;
      border: 1px solid #fff;
      color: #fff;
      padding: 10px 24px;
      border-radius: 6px;
      font-size: 18px;
      transition: all 0.4s;
    }
    .btnMain:hover {
      background-color: #fff;
      color: #a0a4e9;
      transition: all 0.4s;
    }
  }

  .bannerTit {
    margin: 80px 0 40px;
    font-size: 32px;
    font-weight: bold;

    @media screen and (max-width: 505px) {
      font-size: 20px;
    }
  }

  .contentBannerWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;

    @media screen and (max-width: 1225px) {
      display: flex;
      flex-direction: column;
    }

    .boxBanner {
      position: relative;
      height: 400px;
      color: #fff;
      overflow: hidden;
      cursor: pointer;

      @media screen and (max-width: 505px) {
        width: 400px;
      }
      @media screen and (max-width: 425px) {
        width: 425px;
      }
      @media screen and (max-width: 340px) {
        width: 310px;
      }
      @media screen and (max-width: 320px) {
        width: 320px;
      }

      img {
        transition: all 0.3s;
      }
    }

    .boxBanner:hover img {
      transform: scale(1.1);
      transition: all 0.3s;
    }

    .bannerDescription {
      position: absolute;
      left: 30px;
      top: 80px;
      width: 80%;
      height: 80%;
      font-size: 18px;
      line-height: 1.6;
      font-weight: 300;
      word-break: keep-all;

      @media screen and (max-width: 340px) {
        top: 50px;
      }

      .descriptionTit {
        font-size: 26px;
        font-weight: bold;
        margin-bottom: 12px;
      }
      b {
        font-weight: bold;
      }

      .btnBanner {
        display: block;
        background-color: unset;
        border: 1px solid #fff;
        border-radius: 4px;
        color: #fff;
        padding: 10px 12px;
        margin-top: 40px;
        transition: all 0.3s;
      }

      .btnBanner:hover {
        background-color: #fff;
        color: #333;
        transition: all 0.3s;
      }
    }
  }

  .currentBannerWrapper {
    padding-bottom: 60px;

    .currentBanner {
      .sectionInner {
        max-width: 1200px;
        width: 100%;
        display: flex;
        justify-content: space-between;

        @media screen and (max-width: 710px) {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 30px;
        }
      }

      background-color: #5e3dea;
      padding: 80px 0;
      text-align: center;
      color: #60e749;
      font-weight: bold;

      .countBox {
        width: calc(33.3% - 30px);
        width: 100%;

        .descriptionTit {
          font-size: 20px;
          margin-bottom: 30px;

          @media screen and (max-width: 710px) {
            margin-bottom: 0px;
          }
        }
      }
    }
  }

  .challengeBannerWrapper {
    margin-top: 60px;
    padding-bottom: 60px;

    .challengeBanner {
      border-radius: 12px;
      overflow: hidden;
    }
  }
}
